import { Dispatch, ReactElement, SetStateAction, useState } from "react"
import { MobileHeader } from "@rp/react/web/common/base-components"
import { IconButton as ButtonIcon, SxProps } from "@rp/react/web/mui/core"
import { Search } from "@rp/react/web/mui/icons"
import { HubFilterInput } from "../HubFilterInput"

type Props = {
  filterTerm: string
  setFilterTerm: (value: string) => void
  placeholder: string
  title?: string
  hideSearch?: boolean
  sx?: SxProps
  right?: ReactElement
  input?: [boolean, Dispatch<SetStateAction<boolean>>]
}

export const SearchMobileHeader = ({
  filterTerm,
  setFilterTerm,
  placeholder,
  title,
  hideSearch = false,
  sx,
  right,
  input,
}: Props) => {
  const inner = useState(false)
  const [isOpen, setIsOpen] = input ?? inner

  const rightContent = isOpen ? (
    <HubFilterInput
      filterTerm={filterTerm}
      placeholder={placeholder}
      onChange={setFilterTerm}
      onClose={setIsOpen}
    />
  ) : (
    <>
      {right}
      <ButtonIcon role="hub-header-mobile-button-icon" onClick={() => setIsOpen(true)}>
        <Search />
      </ButtonIcon>
    </>
  )

  return (
    <MobileHeader
      sx={sx}
      name={title}
      showBackArrow
      hideLogo
      rightContent={hideSearch ? null : rightContent}
    />
  )
}
